<template>
  <div id="admin-appeal">
    <el-table
      :data="tempData"
      style="width: 100%">
      <el-table-column
        label="申诉流水号"
        prop="appeal_id">
      </el-table-column>
      <el-table-column
        label="申请方"
        prop="appealer">
      </el-table-column>
      <el-table-column
        label="订单号"
        prop="order_id">
      </el-table-column>
      <el-table-column
        label="申诉时间"
        prop="createTime">
      </el-table-column>
      <el-table-column
        label="状态">
        <template slot-scope="scope">
          {{ scope.row.appeal_status == 0 ? '申诉中' : scope.row.appeal_status == 1 ? '申诉通过' : '申诉不通过' }}
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="props">
          <el-button
            type="text"
            size="mini"
            @click="handleDetail(props.row)">详情</el-button>
          <el-button
            type="text"
            size="mini"
            @click="adopt(props.row)" v-if="props.row.appeal_status == 0">通过</el-button>
          <el-button
            type="text"
            size="mini"
            @click="refuse(props.row)" v-if="props.row.appeal_status == 0">拒绝</el-button>
          <el-button
            type="text"
            v-if="props.row.appeal_status != 0"
            size="mini"
            @click="handleDelete(props.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--分页-->
    <pagination :page="curPage" :limit="pageSize" :total="totalData" @pagination="paginationChange"/>
    <!-- 申诉原因 -->
    <el-dialog
      title="申诉原因"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose">
      <div class="item">
        {{ order.reason || '无填写原因' }}
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import {getAppeal, changeAppeal} from '../../../api/index'
  import pagination from '@/components/Pagination/index'

  export default {
    data() {
      return {
        curPage: 1,
        pageSize: 10,
        totalData: 0,
        tempData: [],
        order: {},
        dialogVisible: false
      }
    },
    components: {
      pagination
    },
    activated() {
      this.getAppealData()
    },
    methods: {
      getAppealData() {
        this.tempData = []
        let params = {
          page: this.curPage,
          limit: this.pageSize
        }
        getAppeal(params).then(res => {
          this.tempData = res.message
          this.totalData = res.total
        })
      },
      paginationChange(data) { // 分页改变
        this.curPage = data.page
        this.pageSize = data.limit
        this.getAppealData()
      },
      handleDetail(item) {
        this.order = item
        this.dialogVisible = true
      },
      handleDelete(item) {
        this.$confirm('确认删除该申述吗', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          changeAppeal({
            appeal_id: item.appeal_id,
            type: 1
          }).then(res => {
            if (res.success_code === 200) {
              this.$message.success('删除成功')
              this.getAppealData()
            }
          })
        }).catch(() => {         
        })
      },
      handleClose(done) {
        done()
      },
      adopt(item) {
        this.$confirm('确认通过该申述吗', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          changeAppeal({
            appeal_id: item.appeal_id,
            appeal_status: 1
          }).then(res => {
            if (res.success_code === 200) {
              this.$message.success('通过申述成功')
              this.getAppealData()
            }
          })
        }).catch(() => {         
        })
      },
      refuse(item) {
        this.$confirm('确认拒绝该申述吗', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          changeAppeal({
            appeal_id: item.appeal_id,
            appeal_status: 2
          }).then(res => {
            if (res.success_code === 200) {
              this.$message.success('拒绝申述成功')
              this.getAppealData()
            }
          })
        }).catch(() => {         
        })
      }
    },
  }
</script>

<style scoped>

</style>



